body {
  background-color: #212121;
  color: #FAFAFA;
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: #E91E63;
}

a:hover {
  color: #D0054A;
  text-decoration: underline;
}

a:active {
  color: #FF387D;
}

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
  line-height: 1.5em;
}

h1 {
  font-size: 4em;
  margin: 0;
}

h2 {
  font-size: 2em;
  margin: 1em 0;
}

.grid {
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex: 0 1 auto;
  flex-flow: row wrap;
}

.center {
  margin: 0 auto;
  justify-content: center;
  text-align: center;
}

.grid > [class*=col-], .grid > .row {
  box-sizing: border-box;
  flex: 0 0 auto;
  flex-basis: 100%;
  max-width: 100%;
  padding: 0 12px;
  flex-direction: row;
}
.grid > .col-1-xs {
  flex-basis: 8.3333333333%;
  max-width: 8.3333333333%;
}
.grid > .col-2-xs {
  flex-basis: 16.6666666667%;
  max-width: 16.6666666667%;
}
.grid > .col-3-xs {
  flex-basis: 25%;
  max-width: 25%;
}
.grid > .col-4-xs {
  flex-basis: 33.3333333333%;
  max-width: 33.3333333333%;
}
.grid > .col-5-xs {
  flex-basis: 41.6666666667%;
  max-width: 41.6666666667%;
}
.grid > .col-6-xs {
  flex-basis: 50%;
  max-width: 50%;
}
.grid > .col-7-xs {
  flex-basis: 58.3333333333%;
  max-width: 58.3333333333%;
}
.grid > .col-8-xs {
  flex-basis: 66.6666666667%;
  max-width: 66.6666666667%;
}
.grid > .col-9-xs {
  flex-basis: 75%;
  max-width: 75%;
}
.grid > .col-10-xs {
  flex-basis: 83.3333333333%;
  max-width: 83.3333333333%;
}
.grid > .col-11-xs {
  flex-basis: 91.6666666667%;
  max-width: 91.6666666667%;
}
.grid > .col-12-xs {
  flex-basis: 100%;
  max-width: 100%;
}
.grid.center-xs {
  justify-content: center;
  text-align: center;
}

